.svg-try {
  position: absolute;
}
.svg-try-half path {
  stroke-dasharray: 280 280;
  stroke-dashoffset: 280;
  animation: draw 2s ease-in-out infinite;
}
.svg-try-pause {
  top: 39px;
}
.svg-try-pause path {
  stroke-dasharray: 280 280;
  stroke-dashoffset: 280;
  animation: drawReverse 2s ease-in-out infinite;
}
.svg-try-full path {
  stroke-dasharray: 280 280;
  stroke-dashoffset: 280;
  animation: draw 2s ease-in-out infinite;
}

@keyframes draw {
  from {
    stroke-dashoffset: 280;
  }
  to {
    stroke-dashoffset: 0;
  }
}
@keyframes drawReverse {
  from {
    stroke-dashoffset: 0;
  }
  to {
    stroke-dashoffset: 280;
  }
}
.media-controls {
  cursor: pointer;
}
.media-controls__play {
  stroke-dasharray: 280 280;
  animation: drawPlayReverse 0.2s cubic-bezier(0.3, 0.13, 0.6, 0.89) forwards;
  pointer-events: none;
}
.media-controls__pause {
  stroke-dasharray: 40 280;
  animation: drawPauseReverse 0.2s cubic-bezier(0.3, 0.13, 0.6, 0.89) forwards;
  pointer-events: none;
}

@keyframes drawPlay {
  0% {
    stroke-dashoffset: 0;
  }
  100% {
    stroke-dashoffset: 240;
  }
}
@keyframes drawPause {
  0% {
    stroke-dashoffset: -200;
  }
  75% {
    stroke-dashoffset: 0;
  }
  90% {
    transform: translateY(0px);
  }
  100% {
    transform: translateY(6.5px);
  }
}
.play-ani {
  animation: drawPlay 0.2s cubic-bezier(0.3, 0.13, 0.6, 0.89) forwards;
}

.pause-ani {
  animation: drawPause 0.3s cubic-bezier(0.3, 0.13, 0.6, 0.89) forwards;
}

@keyframes drawPauseReverse {
  0% {
    stroke-dashoffset: 0;
  }
  100% {
    stroke-dashoffset: -100;
  }
}
@keyframes drawPlayReverse {
  0% {
    stroke-dashoffset: 242;
  }
  100% {
    stroke-dashoffset: 100;
  }
}
.inactive-dash {
  -webkit-stroke-dasharray: none !important;
  -moz-stroke-dasharray: none !important;
  -o-stroke-dasharray: none !important;
  stroke-dasharray: none !important;
}
.inactive-dash-pause {
  display: none;
}

.tab-content--error {
  animation: tabError 1s cubic-bezier(0.3, 0.13, 0.6, 0.89) forwards;
}

@keyframes tabError {
  0% {
    transform: translate(-52%, -50%);
  }
  25% {
    transform: translate(-48%, -50%);
  }
  50% {
    transform: translate(-52%, -50%);
  }
  100% {
    transform: translate(-50%, -50%);
  }
}
